export function challengeToUrl (challenge) {
  return {
    'lane': {
      'url': 'https://raw.githubusercontent.com/TuSimple/tusimple-benchmark/master/doc/lane_detection/',
      'readme': 'readme.md',
      'guideline': 'guideline.md',
      'assets': 'assets'
    },
    'velocity': {
      'url': 'https://raw.githubusercontent.com/TuSimple/tusimple-benchmark/master/doc/velocity_estimation/',
      'readme': 'readme.md',
      'guideline': 'guideline.md',
      'assets': 'assets'
    }
  }[challenge]
}



