<template lang="html">
  <div v-html="compiledMarkdown" class="marked"></div>
</template>

<script>
  import { challengeToUrl } from '@/config'

  var marked = require('marked')
  marked.setOptions({
    renderer: new marked.Renderer(),
    gfm: true,
    tables: true,
    breaks: false,
    pedantic: false,
    sanitize: false,
    smartLists: true,
    smartypants: false
  })

  export default{
    name: 'mark',
    data () {
      let url = challengeToUrl(this.$route.params.id)['url']
      let page = challengeToUrl(this.$route.params.id)[this.$route.params.page]
      let assets = challengeToUrl(this.$route.params.id)['assets']
      this.load(url + page)
      return {
        input: '# Loading...',
        url: url,
        page: page,
        assets: assets
      }
    },
    methods: {
      async load (url) {
        let res = await fetch(url)
        res = await res.text()
        this.input = res
      }
    },
    computed: {
      compiledMarkdown: function () {
        let target = this.input.replace(new RegExp(this.assets + '/', 'g'), this.url + this.assets + '/')
        return marked(target)
      }
    },
    watch: {
      '$route' (to, from) {
        this.url = challengeToUrl(to.params.id)['url']
        this.page = challengeToUrl(to.params.id)[to.params.page]
        this.assets = challengeToUrl(to.params.id)['assets']
        this.load(this.url + this.page)
      }
    }
  }
</script>

<style lang='scss'>
  .marked{
      font-size: large;
      font-family: "Avenir Next";
      p{
        >img{
          max-width: 100%;
        }
      }
      h1,h2,h3{
        border-bottom: 1px solid rgba(0, 0, 0, 0.1);
      }
      h1{
        padding-bottom: 10px;
      }
      h2{
        padding-bottom: 5px;
      }
  }
</style>



