import Vue from 'vue'
import Router from 'vue-router'
import Hello from '@/components/Hello'
import Mark from '@/components/Mark'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: Hello
    },
    {
      path: '/challenge/:id/:page',
      name: 'challenge',
      component: Mark
    },
    {
      path: '/*',
      component: {
        template: `
          <div style="padding: 40px;">
            <h1>404</h1>
            <p>Your request path is {{ $route.path }}</p>
            <pre>{{ $route }}</pre>
          </div>
        `.replace(/\s+/g, ' ')
      }
    }
  ]
})



